.wrapper {
    position: absolute;
    display: table;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.borderCell {
    display: table-cell;
    justify-content: center;
    vertical-align: middle;
}

.borderWrap {
    border: thick ridge #6e6e6e;
    position: relative;
    width: 300px;
    height: 500px;
    margin-left: auto;
    margin-right: auto;
}

.hud {
    background-color: #6e6e6e;
    height: 20px;
    width: 40%;
    text-align: center;
    position: relative;
    top: 20%;
    left: 30%;
    color: #ffffff;
}
.buttonSet {
    display: flex;
    position: absolute;
    justify-content: space-around;
    flex-wrap: wrap;
    left: 10%;
    bottom: 0;
    width: 80%;
    height: 50%;
}

.btnSpace {
    height: 15%;
    width: 20%;
    margin: 5%;
}

.slideContainer {
    position: relative;
    top: 30%;
    width: 225px;
    margin-left: auto;
    margin-right: auto;
    color: #ffffff;
}

.volSlider {
    width: 100%;    
}

@media only screen and (min-width: 768px) {
    .borderWrap {
        width: 70%;
        height: 70%;
    }
    .buttonSet {
        bottom: 5%;
    }
    .hud {
        height: 35px;
        font-size: 25px;
    }
}
@media only screen and (min-width: 1000px) {
    .buttonSet {
        bottom: 15%;
    }
    .slideContainer {
        top: 20%;
    }
    .hud {
        top: 10%;
        height: 35px;
        font-size: 25px;
    }
    .borderWrap {
        height: 85%;
    }
}
